import React, {SyntheticEvent, useEffect, useState} from 'react';
import Slider from '@mui/material/Slider';
import styles from './MyRangeSlider.module.scss';
import {Box} from "@mui/material";
import {useAppDispatch} from "../../../hooks/useAppDispatch";
import {setMinAndMaxCardsCount, setMinAndMaxCurrentCardsCount} from "../../../store/reducers/pack/pack.slice";
import {useAppSelector} from "../../../hooks/useAppSelector";

const minDistance = 2;

export const MyRangeSlider = () => {
    const dispatch = useAppDispatch()
    const min = useAppSelector(state => state.pack.minCardsCount)
    const max = useAppSelector(state => state.pack.maxCardsCount)
    const currentMin = useAppSelector(state => state.pack.currentMinCardsCount)
    const currentMax = useAppSelector(state => state.pack.currentMaxCardsCount)

    const [value, setValue] = useState<number[]>([0, 57]);

    useEffect(() => {
        console.log(value)
        dispatch(setMinAndMaxCurrentCardsCount(value))
    }, [value])

    const handleChange = (event: Event | SyntheticEvent<Element, Event>, newValue: number | number[], activeThumb: number,) => {
        if (!Array.isArray(newValue)) {
            return;
        }

        if (newValue[1] - newValue[0] < minDistance) {
            if (activeThumb === 0) {
                const clamped = Math.min(newValue[0], 100 - minDistance);
                setValue([clamped, clamped + minDistance]);
                // dispatch(setMinAndMaxCardsCount([clamped, clamped + minDistance]))
            } else {
                const clamped = Math.max(newValue[1], minDistance);
                setValue([clamped - minDistance, clamped]);
                // dispatch(setMinAndMaxCardsCount([clamped - minDistance, clamped]))
            }
        } else {
            setValue(newValue as number[]);
            // dispatch(setMinAndMaxCardsCount(newValue as number[]))
        }
    };

    return (
        <div className={styles.rangeBox}>
            <div className={styles.value}>{value[0]}</div>
            <Box sx={{width: 200}}>
                <Slider
                    getAriaLabel={() => 'Minimum distance shift'}
                    value={value}
                    max={max}
                    min={min}
                    // @ts-ignore
                    onChangeCommitted={handleChange}
                    disableSwap
                />
            </Box>
            <div className={styles.value}>{value[1]}</div>
            <div className={styles.value}>icon</div>
        </div>
    );
}