import {instance} from "../api/axios";
import {IAllPacksWithParams, IPacksRequestParams} from "../types/packs";

export const packService = {
    getAll(params: IPacksRequestParams) {
        return instance.get<IAllPacksWithParams>('cards/pack', {
                params: {
                    page: params.page,
                    pageCount: params.pageCount,
                    min: params.min,
                    max: params.max
                }
            }
        ).then(data => data.data)
    }
}